    margin-right: 23px; } }

.btn {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  font-size: 14px;
  font-family: "RobotoCondensed-Bold", arial, sans-serif;
  line-height: 16px;
  color: #ffffff;
  min-width: 180px;
  min-height: 45px;
  padding: 14px 12px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.05em;
  cursor: pointer;
  background: transparent; }

.btn--outline {
  margin: 0 0 0 38px; }

.btn--more {
  color: #00b2ff;
  border-color: #00b2ff; }

.btn--question {
  min-width: 270px;
  min-height: 45px;
  padding: 14px 18px; }

/* Tablet */
@media (max-width: 1024px) {
  .btn--outline {
    display: none; } }

.logo__link {
  display: flex; }

.logo__text {
  display: flex;
  flex-direction: column;
  margin: 0 0 0 16px; }

.logo__title {
  font-size: 25px;
  font-family: "RobotoCondensed-Bold", arial, sans-serif;
  line-height: 29px;
  color: #ffffff; }

.logo__title--darker {
  color: #203c42; }

.logo__subtitle {
  font-size: 12px;
  font-family: "Roboto-Medium", arial, sans-serif;
  line-height: 14px;
  color: #c4c4c4; }

.logo__subtitle--darker {
  color: #788891; }

footer .logo__subtitle {
  color: #788891; }

/* Mobile */
@media (max-width: 768px) {
  .logo__text {
    display: none; } }

.footer {
  padding: 65px 0 0 0; }

.footer__container {
  display: flex;
  flex-direction: column; }

.footer__content {
  display: flex;
  justify-content: space-between; }

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.footer__title {
  font-size: 15px;
  font-family: "RobotoCondensed-Bold", arial, sans-serif;
  line-height: 16px;
  color: #1b2d37;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 0 0 28px 0; }

.footer__text {
  font-size: 15px;
  font-family: "PTMono-Regular", monospace;
  line-height: 25px;
  color: #1b2d37;
  opacity: 0.5; }

.footer__copyright {
  display: flex;
  justify-content: space-between; }

.footer__copyright-text {
  font-size: 13px;
  font-family: "PTMono-Regular", monospace;
  line-height: 24px;
  color: #1b2d37;
  opacity: 0.5; }

.social__icon {
  width: 26px;
  height: 26px; }

.social__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.advantages {
  padding-top: 109px; }

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.advantages__item {
  width: calc(50% - 30px);
  min-height: 300px;
  padding: 40px 44px 42px 44px;
  margin: 0 0 30px 0;
  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff; }
  .advantages__item:last-child, .advantages__item:nth-last-child(2) {
    margin-bottom: 0; }
  .advantages__item:hover {
    background-color: #00b2ff;
