  .advantages__item:hover .advantages__title {
    color: #ffffff; }
  .advantages__item:hover .advantages__icon {
    fill: #ffffff; }

.advantages__title {
  font-size: 24px;
  font-family: "RobotoCondensed-Regular", arial, sans-serif;
  line-height: 34px;
  color: #00b2ff;
