  z-index: 1;
  padding: 53px 63px 63px 43px;
  background: center no-repeat transparent;
  background-size: cover; }
  .services__link + .services__link {
    margin-left: 30px; }
  .services__link::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
