    opacity: 0.7;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0; }
  .services__link::after {
    content: "";
    display: block;
    width: 63px;
    height: 63px;
    background: url("../img/icon-vector-right.svg") center no-repeat #00b2ff;
    position: absolute;
    bottom: 0;
    right: 0; }

